"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

interface TimeSlot {
  id: string
  day: string
  startTime: string
  endTime: string
  subject: string
  teacher: string
  class: string
  room: string
  type: "lecture" | "lab" | "tutorial" | "exam"
}

interface ClassSchedule {
  class: string
  schedule: TimeSlot[]
}

export default function TimetablePage() {
  const [selectedClass, setSelectedClass] = useState<string>("10A")
  const [selectedView, setSelectedView] = useState<"class" | "teacher">("class")
  const [selectedTeacher, setSelectedTeacher] = useState<string>("Dr. Sarah Wilson")
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [editingSlot, setEditingSlot] = useState<TimeSlot | null>(null)

  // Mock data
  const [timeSlots, setTimeSlots] = useState<TimeSlot[]>([
    {
      id: "1",
      day: "Monday",
      startTime: "09:00",
      endTime: "10:00",
      subject: "Mathematics",
      teacher: "Dr. Sarah Wilson",
      class: "10A",
      room: "Room 101",
      type: "lecture"
    },
    {
      id: "2",
      day: "Monday",
      startTime: "10:15",
      endTime: "11:15",
      subject: "Physics",
      teacher: "Ms. Emily Davis",
      class: "10A",
      room: "Lab 301",
      type: "lab"
    },
    {
      id: "3",
      day: "Monday",
      startTime: "11:30",
      endTime: "12:30",
      subject: "English",
      teacher: "Mr. David Brown",
      class: "10A",
      room: "Room 205",
      type: "lecture"
    },
    {
      id: "4",
      day: "Tuesday",
      startTime: "09:00",
      endTime: "10:00",
      subject: "Chemistry",
      teacher: "Dr. Sarah Wilson",
      class: "10A",
      room: "Lab 201",
      type: "lab"
    },
    {
      id: "5",
      day: "Tuesday",
      startTime: "10:15",
      endTime: "11:15",
      subject: "Mathematics",
      teacher: "Dr. Sarah Wilson",
      class: "10A",
      room: "Room 101",
      type: "tutorial"
    },
    // Add more slots for other classes
    {
      id: "6",
      day: "Monday",
      startTime: "09:00",
      endTime: "10:00",
      subject: "English Literature",
      teacher: "Mr. David Brown",
      class: "9B",
      room: "Room 205",
      type: "lecture"
    },
    {
      id: "7",
      day: "Monday",
      startTime: "10:15",
      endTime: "11:15",
      subject: "Biology",
      teacher: "Ms. Emily Davis",
      class: "9B",
      room: "Lab 302",
      type: "lab"
    }
  ])

  const classes = ["9A", "9B", "10A", "10B", "11A", "11B", "12A", "12B"]
  const teachers = ["Dr. Sarah Wilson", "Mr. David Brown", "Ms. Emily Davis"]
  const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"]
  const timeSlotOptions = [
    { start: "09:00", end: "10:00" },
    { start: "10:15", end: "11:15" },
    { start: "11:30", end: "12:30" },
    { start: "13:30", end: "14:30" },
    { start: "14:45", end: "15:45" },
    { start: "16:00", end: "17:00" }
  ]

  const getScheduleForClass = (className: string) => {
    return timeSlots.filter(slot => slot.class === className)
  }

  const getScheduleForTeacher = (teacherName: string) => {
    return timeSlots.filter(slot => slot.teacher === teacherName)
  }

  const handleAddSlot = () => {
    setEditingSlot(null)
    setIsDialogOpen(true)
  }

  const handleEditSlot = (slot: TimeSlot) => {
    setEditingSlot(slot)
    setIsDialogOpen(true)
  }

  const handleDeleteSlot = (id: string) => {
    setTimeSlots(timeSlots.filter(slot => slot.id !== id))
  }

  const handleSaveSlot = (formData: FormData) => {
    const slotData = {
      id: editingSlot?.id || Date.now().toString(),
      day: formData.get("day") as string,
      startTime: formData.get("startTime") as string,
      endTime: formData.get("endTime") as string,
      subject: formData.get("subject") as string,
      teacher: formData.get("teacher") as string,
      class: formData.get("class") as string,
      room: formData.get("room") as string,
      type: formData.get("type") as "lecture" | "lab" | "tutorial" | "exam"
    }

    if (editingSlot) {
      setTimeSlots(timeSlots.map(s => s.id === editingSlot.id ? slotData : s))
    } else {
      setTimeSlots([...timeSlots, slotData])
    }

    setIsDialogOpen(false)
    setEditingSlot(null)
  }

  const renderTimetableGrid = (schedule: TimeSlot[]) => {
    const grid: { [key: string]: { [key: string]: TimeSlot | null } } = {}
    
    // Initialize grid
    days.forEach(day => {
      grid[day] = {}
      timeSlotOptions.forEach(timeSlot => {
        grid[day][`${timeSlot.start}-${timeSlot.end}`] = null
      })
    })

    // Fill grid with schedule data
    schedule.forEach(slot => {
      const timeKey = `${slot.startTime}-${slot.endTime}`
      if (grid[slot.day] && grid[slot.day][timeKey] !== undefined) {
        grid[slot.day][timeKey] = slot
      }
    })

    return (
      <div className="overflow-x-auto">
        <table className="w-full border-collapse border border-gray-300">
          <thead>
            <tr className="bg-gray-50">
              <th className="border border-gray-300 p-3 text-left font-medium">Time</th>
              {days.map(day => (
                <th key={day} className="border border-gray-300 p-3 text-center font-medium min-w-[150px]">
                  {day}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {timeSlotOptions.map(timeSlot => (
              <tr key={`${timeSlot.start}-${timeSlot.end}`}>
                <td className="border border-gray-300 p-3 font-medium bg-gray-50">
                  {timeSlot.start} - {timeSlot.end}
                </td>
                {days.map(day => {
                  const slot = grid[day][`${timeSlot.start}-${timeSlot.end}`]
                  return (
                    <td key={`${day}-${timeSlot.start}`} className="border border-gray-300 p-2 h-20 align-top">
                      {slot ? (
                        <div className="bg-blue-50 border border-blue-200 rounded p-2 h-full flex flex-col justify-between">
                          <div>
                            <div className="font-medium text-sm text-blue-900">{slot.subject}</div>
                            <div className="text-xs text-blue-700">{slot.teacher}</div>
                            <div className="text-xs text-blue-600">{slot.room}</div>
                          </div>
                          <div className="flex justify-between items-center mt-1">
                            <Badge variant="outline" className="text-xs">
                              {slot.type}
                            </Badge>
                            <div className="flex space-x-1">
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-6 w-6 p-0 text-blue-600 hover:text-blue-800"
                                onClick={() => handleEditSlot(slot)}
                              >
                                ✎
                              </Button>
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-6 w-6 p-0 text-red-600 hover:text-red-800"
                                onClick={() => handleDeleteSlot(slot.id)}
                              >
                                ✕
                              </Button>
                            </div>
                          </div>
                        </div>
                      ) : (
                        <div className="h-full flex items-center justify-center text-gray-400 text-sm">
                          Free
                        </div>
                      )}
                    </td>
                  )
                })}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    )
  }

  const getTimetableStats = () => {
    const totalSlots = timeSlots.length
    const lectureSlots = timeSlots.filter(s => s.type === "lecture").length
    const labSlots = timeSlots.filter(s => s.type === "lab").length
    const tutorialSlots = timeSlots.filter(s => s.type === "tutorial").length

    return {
      totalSlots,
      lectureSlots,
      labSlots,
      tutorialSlots
    }
  }

  const stats = getTimetableStats()

  return (
    <div className="flex-1 space-y-6 p-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Timetable</h1>
          <p className="text-muted-foreground">
            Manage class schedules and time slots
          </p>
        </div>
        <Button onClick={handleAddSlot}>
          Add Time Slot
        </Button>
      </div>

      {/* Timetable Statistics */}
      <div className="grid gap-4 md:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Slots</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalSlots}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Lectures</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-blue-600">{stats.lectureSlots}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Lab Sessions</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.labSlots}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Tutorials</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{stats.tutorialSlots}</div>
          </CardContent>
        </Card>
      </div>

      <Tabs value={selectedView} onValueChange={(value) => setSelectedView(value as "class" | "teacher")} className="space-y-4">
        <TabsList>
          <TabsTrigger value="class">Class Timetable</TabsTrigger>
          <TabsTrigger value="teacher">Teacher Schedule</TabsTrigger>
        </TabsList>

        <TabsContent value="class" className="space-y-4">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Class Timetable</CardTitle>
                  <CardDescription>
                    View and manage class schedules
                  </CardDescription>
                </div>
                <div className="flex items-center space-x-2">
                  <Label htmlFor="class-select">Select Class:</Label>
                  <Select value={selectedClass} onValueChange={setSelectedClass}>
                    <SelectTrigger className="w-[120px]">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {classes.map((cls) => (
                        <SelectItem key={cls} value={cls}>
                          {cls}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardHeader>
            <CardContent>
              {renderTimetableGrid(getScheduleForClass(selectedClass))}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="teacher" className="space-y-4">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Teacher Schedule</CardTitle>
                  <CardDescription>
                    View teacher's weekly schedule
                  </CardDescription>
                </div>
                <div className="flex items-center space-x-2">
                  <Label htmlFor="teacher-select">Select Teacher:</Label>
                  <Select value={selectedTeacher} onValueChange={setSelectedTeacher}>
                    <SelectTrigger className="w-[180px]">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {teachers.map((teacher) => (
                        <SelectItem key={teacher} value={teacher}>
                          {teacher}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardHeader>
            <CardContent>
              {renderTimetableGrid(getScheduleForTeacher(selectedTeacher))}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* All Time Slots List */}
      <Card>
        <CardHeader>
          <CardTitle>All Time Slots</CardTitle>
          <CardDescription>
            Complete list of all scheduled time slots
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Day</TableHead>
                <TableHead>Time</TableHead>
                <TableHead>Subject</TableHead>
                <TableHead>Teacher</TableHead>
                <TableHead>Class</TableHead>
                <TableHead>Room</TableHead>
                <TableHead>Type</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {timeSlots
                .sort((a, b) => {
                  const dayOrder = days.indexOf(a.day) - days.indexOf(b.day)
                  if (dayOrder !== 0) return dayOrder
                  return a.startTime.localeCompare(b.startTime)
                })
                .map((slot) => (
                  <TableRow key={slot.id}>
                    <TableCell className="font-medium">{slot.day}</TableCell>
                    <TableCell>{slot.startTime} - {slot.endTime}</TableCell>
                    <TableCell>{slot.subject}</TableCell>
                    <TableCell>{slot.teacher}</TableCell>
                    <TableCell>{slot.class}</TableCell>
                    <TableCell>{slot.room}</TableCell>
                    <TableCell>
                      <Badge variant="outline">
                        {slot.type}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex space-x-2">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handleEditSlot(slot)}
                        >
                          Edit
                        </Button>
                        <Button
                          variant="destructive"
                          size="sm"
                          onClick={() => handleDeleteSlot(slot.id)}
                        >
                          Delete
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {/* Add/Edit Time Slot Dialog */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="sm:max-w-[500px]">
          <form action={handleSaveSlot}>
            <DialogHeader>
              <DialogTitle>
                {editingSlot ? "Edit Time Slot" : "Add New Time Slot"}
              </DialogTitle>
              <DialogDescription>
                {editingSlot 
                  ? "Update time slot information below."
                  : "Enter details to add a new time slot to the timetable."
                }
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="day" className="text-right">
                  Day
                </Label>
                <Select name="day" defaultValue={editingSlot?.day || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select day" />
                  </SelectTrigger>
                  <SelectContent>
                    {days.map((day) => (
                      <SelectItem key={day} value={day}>
                        {day}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="startTime" className="text-right">
                  Start Time
                </Label>
                <Input
                  id="startTime"
                  name="startTime"
                  type="time"
                  defaultValue={editingSlot?.startTime || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="endTime" className="text-right">
                  End Time
                </Label>
                <Input
                  id="endTime"
                  name="endTime"
                  type="time"
                  defaultValue={editingSlot?.endTime || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="subject" className="text-right">
                  Subject
                </Label>
                <Select name="subject" defaultValue={editingSlot?.subject || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select subject" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Mathematics">Mathematics</SelectItem>
                    <SelectItem value="Physics">Physics</SelectItem>
                    <SelectItem value="Chemistry">Chemistry</SelectItem>
                    <SelectItem value="Biology">Biology</SelectItem>
                    <SelectItem value="English">English</SelectItem>
                    <SelectItem value="History">History</SelectItem>
                    <SelectItem value="Geography">Geography</SelectItem>
                    <SelectItem value="Computer Science">Computer Science</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="teacher" className="text-right">
                  Teacher
                </Label>
                <Select name="teacher" defaultValue={editingSlot?.teacher || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select teacher" />
                  </SelectTrigger>
                  <SelectContent>
                    {teachers.map((teacher) => (
                      <SelectItem key={teacher} value={teacher}>
                        {teacher}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="class" className="text-right">
                  Class
                </Label>
                <Select name="class" defaultValue={editingSlot?.class || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select class" />
                  </SelectTrigger>
                  <SelectContent>
                    {classes.map((cls) => (
                      <SelectItem key={cls} value={cls}>
                        {cls}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="room" className="text-right">
                  Room
                </Label>
                <Input
                  id="room"
                  name="room"
                  defaultValue={editingSlot?.room || ""}
                  className="col-span-3"
                  placeholder="e.g., Room 101"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="type" className="text-right">
                  Type
                </Label>
                <Select name="type" defaultValue={editingSlot?.type || "lecture"}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="lecture">Lecture</SelectItem>
                    <SelectItem value="lab">Lab</SelectItem>
                    <SelectItem value="tutorial">Tutorial</SelectItem>
                    <SelectItem value="exam">Exam</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>
                Cancel
              </Button>
              <Button type="submit">
                {editingSlot ? "Update Slot" : "Add Slot"}
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  )
}
